@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* css reset */
* {
	vertical-align: baseline;
	font-weight: inherit;
	font-family: inherit;
	font-style: inherit;
	font-size: 100%;
	border: 0 none;
	outline: 0;
	padding: 0;
  margin: 0;
  
  box-sizing: border-box;
  }
  
  body{
    font-family: 'Roboto', sans-serif;
    font-size: 8px;
  }

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background: url(/nasa-potd/static/media/stars.01adf5d3.svg),
              radial-gradient(#485160, #101114);
  height: 100vh;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;

  padding: 64px 32px;
}

.App-link {
  color: #09d3ac;
}

.image-preview {
  grid-column: 7 / span 10;
  grid-row: span 2; }

.image {
  height: 100%;
  width: 100%;
  object-fit: contain;
  -webkit-filter: drop-shadow(0 10px 40px #000000);
          filter: drop-shadow(0 10px 40px #000000);
  object-position: 0 0; }

.article-text {
  display: inline-block;
  grid-column: 1 / span 5;
  grid-row: 1; }

.article-text * {
  margin-bottom: 2.8vmin; }

article h1 {
  font-size: calc(20px + 3vmin);
  font-weight: 100;
  letter-spacing: 0.5vmin;
  color: white;
  text-align: left;
  text-transform: uppercase; }

article h2 {
  font-size: calc(5px + 1vmin);
  font-weight: 100;
  letter-spacing: 0.5vmin;
  color: white;
  text-align: left;
  text-transform: uppercase; }

article p {
  font-size: calc(1px + 1.4vmin);
  font-weight: 300;
  color: white;
  text-align: left;
  margin-left: span 1;
  line-height: 3.4vmin; }

.nav-controls {
  grid-column: 1 / span 5;
  display: flex;
  justify-content: space-between; }

.button-back, .button-forward, .button-latest, .button-calendar {
  width: 16px;
  height: 16px;
  font-size: 0;
  -webkit-filter: invert(100%);
          filter: invert(100%);
  display: inline-block; }

.button-back {
  background: url(/nasa-potd/static/media/_ionicons_svg_md-play.81c9de12.svg);
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1); }

.button-forward {
  background: url(/nasa-potd/static/media/_ionicons_svg_md-play.81c9de12.svg); }

.button-latest {
  background: url(/nasa-potd/static/media/_ionicons_svg_md-skip-forward.2e5c16df.svg); }

.button-calendar {
  background: url(/nasa-potd/static/media/calendar-regular.17efa687.svg);
  background-repeat: no-repeat; }

article {
  /* grid */
  display: grid;
  height: 100%;
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: 1fr 16px; }

